.details-row {
  display: flex;
  flex-flow: row nowrap;
  font-size: 12px;
}

.details-label {
  width: 30%;
  color: #3e396a;
}

.details-value {
  width: 70%;
  color: var(--gray);
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.details-value canvas {
  border-radius: 4px;
}
